.navbar{
    display: flex;
    flex-direction: row;
    background-color: #707070;
    padding: 0px 20px;
    align-items: center;
    justify-content: space-between;
    color: white;
}
.btn{
    background-color: #4CAF50; /* Green */
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
}
.btn:hover{
    background-color: #3a8d20;
}
input{
    font-size: 16px;
}
@media only screen and (max-width: 600px){
    .navbar{
        flex-direction: column;
    }
}